import LangData from './data/en.json'
import {storageData} from 'utils/storage'

const T = props => {
  const {children} = props

  const RU = 'ru'

  const lang = storageData('language').getValue()

  if (lang === RU && LangData[children]) {
    return LangData[children]
  }
  return children
}

export default T
