import axios from 'axios'
import {responseToCamelCase} from 'utils/toCamelCase'
import {
  curry,
  equals,
  path
} from 'ramda'

const axiosConfig = (api) => ({
  baseURL: api,
  transformResponse: [responseToCamelCase]
})

const UNAUTHORIZED = 401

const errorInterceptors = curry((dispatch, error) => {
  const status = path(['response', 'status'], error)

  if (equals(UNAUTHORIZED, status)) {
    // dispatch({type: `${AUTH}_CLEAR`})
    // eslint-disable-next-line no-console
    console.error('Auth error status: ', status)
  }

  return Promise.reject(error)
})

export default ({dispatch}) => {
  const apiProtocol = 'https://'
  const apiPathname = 'jsonplaceholder.typicode.com'
  const apiLink = apiProtocol + apiPathname

  // This optional code is used to register a token
  // const state = getState()
  // const token = storageData('token').getValue() || path(['auth', 'data', 'token'], state)
  // const headers = {
  //   common: {Authorization: token ? `Token ${token}` : ''}
  // }
  // const instance = axios.create({...axiosConfig(apiLink), headers})

  const instance = axios.create({...axiosConfig(apiLink)})

  instance.interceptors.response.use(
    response => response,
    errorInterceptors(dispatch)
  )

  return instance
}
