import {
  compose,
  curry,
  path,
  pathOr,
  prop
} from 'ramda'

export const getPayloadFromSuccess = prop('data')

export const getPayloadFromError = compose(
  data => Promise.reject(data),
  path(['response', 'data'])
)

export const getDataFromState = curry((name, state) => ({
  loading: path([name, 'loading'], state),
  data: path([name, 'data'], state),
  error: path([name, 'error'], state),
  failed: path([name, 'failed'], state)
}))

export const getListFromState = curry((name, state) => ({
  loading: path([name, 'loading'], state),
  count: pathOr(0, [name, 'data', 'count'], state),
  data: path([name, 'data'], state),
  error: path([name, 'error'], state),
  results: pathOr([], [name, 'data', 'results'], state),
  failed: path([name, 'failed'], state)
}))
