import React, {useState, Suspense} from 'react'
import styled from 'styled-components'
import {mediaQueries} from 'constants/mediaQueries'
import {map, prop} from 'ramda'
import WorksViewer from 'components/WorksViewer'
import Loader from 'components/Loader'

// LazyImports
const WorkCard = React.lazy(() => import('components/Cards/WorkCard'))

const Wrap = styled('div')`
  display: grid;
  grid-template-columns: 1fr 1fr;
  grid-gap: 0 30px;
  & > *:nth-child(even) {
    margin-top: 30px;
  }
  @media ${mediaQueries.tabletS}{
    grid-template-columns: 1fr;
    grid-gap: 30px 0;
    & > *:nth-child(even) {
      margin-top: 0;
    }
  }
  @media ${mediaQueries.mobileL}{
    grid-gap: 10px 0;
  }
`
const CardSkeleton = styled('div')`
  display: flex;
  justify-content: center;
  align-items: center;
  max-width: 100%;
  height: 600px;
`

const Works = (props) => {
  const {
    works
  } = props

  // useStates
  const [imageViewer, setImageViewer] = useState(false)
  const [selectedImages, setSelectedImages] = useState([])

  // Works list
  const worksList = map((item) => {
    const title = prop('title', item)
    const subtitle = prop('subtitle', item)
    const year = prop('year', item)
    const img = prop('cover', item)
    const images = prop('images', item)
    const withDetail = prop('withDetail', item)
    const id = prop('id', item)

    return (
      <Suspense
        fallback={(
          <CardSkeleton>
            <Loader />
          </CardSkeleton>
        )}
        key={id}
      >
        <WorkCard
          title={title}
          subtitle={subtitle}
          year={year}
          img={img}
          withDetail={withDetail}
          onLink={() => {
            setSelectedImages(images)
            setTimeout(() => {
              setImageViewer(true)
            }, 0)
          }}
        />
      </Suspense>
    )
  }, works)

  return (
    <Wrap>
      {worksList}
      <WorksViewer
        open={imageViewer}
        setOpen={setImageViewer}
        setSelectedImages={setSelectedImages}
        images={selectedImages}
      />
    </Wrap>
  )
}

export default Works
